import { Component, OnInit } from '@angular/core';
import {TipoProblema} from '../../tipo-problema/tipo-problema'
import {TipoProblemaService} from '../../tipo-problema/tipo-problema.service'
import {ProblemaService} from '../problema.service'
import{ProblemaComponent} from '../problema.component'
import { Problema } from '../problema';
import {SingleProblem} from './single-problem'
import { single } from 'rxjs/operators';
import {ProblemaFecha} from './problema-fecha'

import {NewProblemService} from './new-problem.service'
import {ProblemaFechaService} from './problema-fecha.service'


@Component({
  selector: 'app-new-problem',
  templateUrl: './new-problem.component.html',
  styleUrls: ['./new-problem.component.css']
})
export class NewProblemComponent implements OnInit {

  tipos : TipoProblema[] = []
  problemas:SingleProblem[] = []
  singleProblem = new SingleProblem()
  problemaFecha = new ProblemaFecha()
  problema : SingleProblem[] = []
  constructor(
    private _tipoProblemaService:TipoProblemaService,
    private _newProblemService : NewProblemService,
    private _problemaFechaService : ProblemaFechaService
  ) { }

  ngOnInit() {
    this.getTiposProblema();
    this.getProblemas();
    this.singleProblem.LATEX_SOURCE_PROBLEMA = '\\text{Problema: } \\sum_{i=1}^{3}x_{i}=x_{1}+x_{2}';
    this.singleProblem.LATEX_SOURCE_SOLUCION = '\\text{Problema: } \\sum_{i=1}^{3}x_{i}=x_{1}+x_{2}';
  
  }
  getProblemas()
  {
    this._newProblemService.getSingleProblems()
    .then(x =>this.problemas = x)
  }

  getTiposProblema()
  {
    this._tipoProblemaService.getTipoProblema()
    .subscribe(x => this.tipos = x)
  }
  getProblemaNombre()
  {
    this._newProblemService.getProblem(this.singleProblem.NOMBRE)
    .subscribe(x => this.problema = x)
  }

  add(e){
    e.preventDefault();
    var nombre = e.target.elements[0].value;
    var categoria = e.target.elements[1].value;
    var problema = e.target.elements[2].value;
    var fechaLiberacionProblema = e.target.elements[3].value;
    var solucion = e.target.elements[4].value;
    var fechaLiberacionSolucion = e.target.elements[5].value;

   var problemaCorregido = "";
   var solucionCorregido = "";
    
   for( var i = 0; i<this.singleProblem.LATEX_SOURCE_PROBLEMA.length;i++)
   {
     if(this.singleProblem.LATEX_SOURCE_PROBLEMA[i] == "\\")
     {
       problemaCorregido += "\\"+"\\"
     }
     else{
      problemaCorregido += this.singleProblem.LATEX_SOURCE_PROBLEMA[i]
     }
   }

       
   for( var i = 0; i<this.singleProblem.LATEX_SOURCE_SOLUCION.length;i++)
   {
     if(this.singleProblem.LATEX_SOURCE_SOLUCION[i] == "\\")
     {
      solucionCorregido += "\\"+"\\"
     }
     else{
      solucionCorregido += this.singleProblem.LATEX_SOURCE_SOLUCION[i]
     }
   }

   this.singleProblem.LATEX_SOURCE_PROBLEMA = problemaCorregido
   this.singleProblem.LATEX_SOURCE_SOLUCION = solucionCorregido


    var valido = true;

    for (var i = 0; i<this.problemas.length;i++) {
      var element = this.problemas[i];
      if (element.NOMBRE == nombre)
      {
        valido = false;
      }
    }

    if(valido)
    {
      this._newProblemService.createProblema(this.singleProblem);
      this.singleProblem = new SingleProblem();

      this.problemaFecha.NOMBRE = nombre;
      this.problemaFecha.FECHA_LIBERACION_PROBLEMA = fechaLiberacionProblema
      this.problemaFecha.FECHA_LIBERACION_SOLUCION = fechaLiberacionSolucion

      this._problemaFechaService.createProblemaDate(this.problemaFecha);
      this.problemaFecha = new ProblemaFecha()
    
    }
  }
}
