import { Component, OnInit } from '@angular/core';
import {Client} from './client';
import {ClientService} from './client.service'
import {Router} from '@angular/router';
import {AppComponent} from '../app.component';

@Component({
  selector: 'app-sesion',
  templateUrl: './sesion.component.html',
  styleUrls: ['./sesion.component.css']
})
export class SesionComponent implements OnInit {

  clients: Client[];
  constructor(
    private _clientService : ClientService,
    private appComponent: AppComponent,
    private router:Router
  ) { }

  ngOnInit() {
    this.getClients();
  }


  loginUser(e)
  {
    e.preventDefault();
    var username = e.target.elements[0].value;
    var pass = e.target.elements[1].value;

    console.log(username + " "+pass);

    for (var i = 0; i<this.clients.length;i++) {
      var element = this.clients[i];
      if(username == element.CORREO && pass == element.PASSWORD)
      {
        if(element.ID_TIPO_CLIENTE == 1)
        {
          this.router.navigate(['admin'])
          this.appComponent.setLogIn(1);
        }
        else
        {
          this.router.navigate(['user'])
          this.appComponent.setLogIn(2);
        }
        break;
      }
    }

   
  }

  getClients(){
    this._clientService.getClients().then(clients => this.clients = clients);
  }

}
