import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {Client} from '../../sesion/client';
import {ClientService} from '../../sesion/client.service';
import {TipoCliente } from '../tipo-cliente';
import {SesionComponent} from '../../sesion/sesion.component'


@Component({
  selector: 'app-new-administrador',
  templateUrl: './new-administrador.component.html',
  styleUrls: ['./new-administrador.component.css']
})
export class NewAdministradorComponent implements OnInit {
  tipoCliente: TipoCliente[]
  TipoCliente: TipoCliente
  clients: Client[];
  cliente: Client;

  constructor(private agregar_admin_clientService: ClientService, 
    private router:Router) {
    
  }

  ngOnInit() {
  }


  agregarAdmin(e)
  {
    console.log("agregarAdmin")
    e.preventDefault();
    var nombre = e.target.elements[0].value;
    var correo = e.target.elements[1].value;
    var pass = e.target.elements[2].value;
    
    var newAdmin = new Client;
    newAdmin.ID_TIPO_CLIENTE = 1;
    newAdmin.ID_CLIENTE = 0; //¿?
    newAdmin.NOMBRE = nombre;
    newAdmin.CORREO = correo;
    newAdmin.PASSWORD = pass;
    
    this.agregar_admin_clientService.create(newAdmin);
  }

}
