import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import {Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import {ProblemaFecha} from './problema-fecha'
@Injectable({
  providedIn: 'root'
})
export class ProblemaFechaService {
  constructor(
    private _http:Http
  ) { }

  createProblemaDate(ProblemDate : ProblemaFecha)
  {
    return this._http.post('/problemDate',ProblemDate)
    .map(data => data.json()).toPromise()
  }
  getProblemasDate(nombre: String)
  {
    return this._http.get('/problemDate/'+nombre)
    .map(data => data.json());
  }

  updateProblemDate(problemDate : ProblemaFecha)
  {
    return this._http.put('/problemDate/',problemDate)
    .map(data => data.json()).toPromise();
  }
}
