import { Component, OnInit, Input } from '@angular/core';
import {Problema} from '../problema'
import {ProblemaService} from '../problema.service'
import {VerProblemaComponent} from '../ver-problema/ver-problema.component'
import {Router} from '@angular/router';

@Component({
  selector: 'app-tabla-problema',
  templateUrl: './tabla-problema.component.html',
  styleUrls: ['./tabla-problema.component.css']
})
export class TablaProblemaComponent implements OnInit {
  @Input() problems : Problema[];
  public problemaSeleccionado : Problema
  verProblemaComponent : VerProblemaComponent
  constructor(
    private router:Router,
    private problemaService : ProblemaService
  ) { }
  ngOnInit() {
  }

  verProblema(p : Problema){
    this.problemaSeleccionado = p
    this.router.navigate(['/verProblema',this.problemaSeleccionado.NOMBRE]);
 
  }
  eliminarProblema(nombre : String)
  {
    console.log(nombre)
    this.problemaService.eliminarProblema(nombre).
    then(status =>
      this.getProblemas()).
      catch(err => console.log(err))
  }
  getProblemas()
  {
    this.problemaService.getProblemas()
    .then(x => this.problems = x);
  }
}
