import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap, Route, Router } from '@angular/router';
import {SingleProblem} from '../new-problem/single-problem'
import {NewProblemService} from '../new-problem/new-problem.service'
import {TipoProblemaService} from '..//../tipo-problema/tipo-problema.service'
import {TipoProblema} from '../../tipo-problema/tipo-problema'
import 'rxjs/Rx'; 

@Component({
  selector: 'app-ver-problema',
  templateUrl: './ver-problema.component.html',
  styleUrls: ['./ver-problema.component.css']
})
export class VerProblemaComponent implements OnInit {

  public problema : SingleProblem;
  nombre : String
  tipoProblemaString : String = ""
  problemLatex : String = "\\text{Hola}"
  solutionLatex : String = ""
  idTipo: number = -1
  tiposProblema : TipoProblema[] = []
  constructor(
    private route : ActivatedRoute,
    private router: Router,
    private newProblemService : NewProblemService,
    private _tipoProblemaService : TipoProblemaService
  ) {
    this.nombre = this.route.snapshot.paramMap.get('nombre')

    this.newProblemService.getProblem(this.nombre)
    .subscribe(p => {this.problema = p[0]
    this.problemLatex = this.problema.LATEX_SOURCE_PROBLEMA
    this.solutionLatex = this.problema.LATEX_SOURCE_SOLUCION
    this.idTipo = this.problema.ID_TIPO_PROBLEMA 
    }
    );
    


    this._tipoProblemaService.getTipoProblema()
    .subscribe(x => 
      {this.tiposProblema = x;
        for(var i = 0; i<this.tiposProblema.length;i++)
        {
          if(this.tiposProblema[i].ID_TIPO_PROBLEMA == this.problema.ID_TIPO_PROBLEMA)
          {
            this.tipoProblemaString = this.tiposProblema[i].TIPO_PROBLEMA;
            console.log("TIPO: "+this.tipoProblemaString)
            break;
          }
        }})

   }

  ngOnInit() {
    console.log("PROBLEMA LATEX: "+this.problemLatex);
  }

}
