import { Component, OnInit } from '@angular/core';
import {Obra} from './obra';
import {ObraService} from './obras.service'
import {Router} from '@angular/router';
import { TipoColeccion } from './tipo-coleccion';

@Component({
  selector: 'app-obras',
  templateUrl: './obras.component.html',
  styleUrls: ['./obras.component.css']
})
export class ObrasComponent implements OnInit {

  obras: Obra[];
  obra: Obra;
  constructor(private obraService: ObraService,
    private router:Router) { }

  ngOnInit() {
    this.cargarObras()
  }

  cargarObras(){
    this.obraService.getObras().then(obras => this.obras = obras);
  }

  newObra(){
    this.router.navigate(['obras-new'])
  }

  editObra(obra){
    this.obra = obra
    this.router.navigate(['/obras-edit', this.obra.ID_OBRA_DE_ARTE]);
  }

  eliminarObra(id){
    console.log(id);
    this.obraService.delete(id);
    this.obras = this.obras.filter(o => o.ID_OBRA_DE_ARTE !== id);
  }
}
