import { Component, OnInit } from '@angular/core';
import { TipoColeccion } from '../tipo-coleccion';
import { TipoTecnica } from '../tipo-tecnica';
import { ActivatedRoute, ParamMap, Route, Router } from '@angular/router';
import { ObraService } from '../obras.service';
import { TipoColeccionService } from '../obras-new/tipo-coleccion.service';
import { Obra } from '../obra';


@Component({
  selector: 'app-obras-edit',
  templateUrl: './obras-edit.component.html',
  styleUrls: ['./obras-edit.component.css']
})
export class ObrasEditComponent implements OnInit {

  obra: Obra;
  idObra: String;
  fechaInicio : Date
  fechaFin : Date
  tiposColecciones: TipoColeccion[]
  tiposTecnicas: TipoTecnica[]
  tipoColeccion: TipoColeccion
  tipoTecnica: TipoTecnica
  imageFile: File
  editada: boolean = false
  error: boolean = false

  constructor(private route: ActivatedRoute, 
    private obraService: ObraService,
    private tipoService: TipoColeccionService,
    private router: Router) {
    this.idObra = this.route.snapshot.paramMap.get('id')
    this.obraService.getObra(this.idObra)
      .subscribe(obra => {
        this.obra = obra[0]
        this.obra.FECHA_INICIO = this.obra.FECHA_INICIO.substring(0,10)
        this.obra.FECHA_FIN = this.obra.FECHA_FIN.substring(0,10)
      });
  }

  ngOnInit() {
    this.cargarTipos()
  }

  cargarTipos() {
    this.tipoService.getTiposColeccion().then(tipos => this.tiposColecciones = tipos)
    this.tipoService.getTiposTecnicas().then(tipos => this.tiposTecnicas = tipos)
  }

  changeImage(event) {
    this.imageFile = <File>event.target.files[0]
  }

  editar(e) {
    e.preventDefault();
    if (this.imageFile){
      this.obraService.uploadImage(this.imageFile).subscribe()
      this.obra.IMG_SOURCE = "images/" + this.imageFile.name
    }
    if (this.validarObra()){
      this.obraService.edit(this.obra)
      this.editada = true
      this.error = false
    } else {
      this.error = true
      this.editada = false
      return
    }
  }

  validarObra(){
    if (this.obra.NOMBRE_ARTISTA === "" || this.obra.NOMBRE_DE_OBRA === "" || 
        this.obra.LARGO === 0 || this.obra.ANCHO === 0 || this.obra.YEAR === 0 || 
        this.obra.ID_TIPO_COLECCION === 0 || this.obra.ID_TIPO_DE_TECNICA === 0 ||
        this.obra.ID_TIPO_COLECCION === 0 || this.obra.ID_TIPO_DE_TECNICA === 0)
        return false
    return true
  }
}
