import { Component, OnInit, Input } from '@angular/core';
import {SingleProblem} from '../new-problem/single-problem'
import {NewProblemService} from '../new-problem/new-problem.service'
import {TipoProblemaService} from '../../tipo-problema/tipo-problema.service'
import {TipoProblema} from '../../tipo-problema/tipo-problema'
import {ProblemaFecha} from '../new-problem/problema-fecha'
import {ProblemaFechaService} from '../new-problem/problema-fecha.service'
@Component({
  selector: 'app-editar-problema',
  templateUrl: './editar-problema.component.html',
  styleUrls: ['./editar-problema.component.css']
})
export class EditarProblemaComponent implements OnInit {
  @Input() problema : SingleProblem
  editProblema = new SingleProblem
  problemasDate : ProblemaFecha[] = []
  tipos : TipoProblema[] = []
  constructor(
    private _newProblemService : NewProblemService,
    private _tipoProblemaService : TipoProblemaService,
    private _problemaFechaService : ProblemaFechaService
  ) { }

  ngOnInit() {
    setTimeout(() => {
      Object.assign(this.editProblema,this.problema);
    console.log(this.editProblema)
    console.log(this.problema)
    this.getTiposProblema();
    this.getProblemasDate(this.problema.NOMBRE);
  });
    
  }

  update()
  {
    this._newProblemService.updateProblem(this.editProblema).
    then(status => this.problema = this.editProblema)
    .catch(err => console.log(err));

    for (var i = 0;i<this.problemasDate.length;i++)
    {
      this._problemaFechaService.updateProblemDate(this.problemasDate[i])
      .then()
      .catch(err => console.log(err))
    }
  }
  getTiposProblema()
  {
    this._tipoProblemaService.getTipoProblema()
    .subscribe(x => this.tipos = x)
  }

  getProblemasDate(nombre : String)
  {
    this._problemaFechaService.getProblemasDate(nombre)
    .subscribe(x => this.problemasDate = x)
  }
}
