import { Injectable } from '@angular/core';
import {Http,  Headers, RequestOptions} from '@angular/http';
import {Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import {SingleProblem} from '../new-problem/single-problem'

@Injectable({
  providedIn: 'root'
})
export class NewProblemService {

  constructor(
    private _http:Http
  ) { }

  createProblema(singleProblem : SingleProblem)
  {
    return this._http.post('/problem',singleProblem)
    .map(data => data.json()).toPromise()
  }

  getSingleProblems()
  {
    return this._http.get('/singleProblem')
    .map(data => data.json()).toPromise()
  }

  getProblem(nombre : String)
  {
    return this._http.get('/singleProblem/'+nombre)
    .map(data => data.json());
  }

  updateProblem(singleProblem : SingleProblem)
  {
    return this._http.put('/singleProblem/',singleProblem)
    .map(data => data.json()).toPromise();
  }

 
}
