import { Component, OnInit } from '@angular/core';
import { TipoColeccionService } from './tipo-coleccion.service';
import { TipoColeccion } from '../tipo-coleccion';
import { TipoTecnica } from '../tipo-tecnica';
import { Obra } from '../obra';
import { Router } from '@angular/router';
import { ObraService } from '../obras.service';

@Component({
  selector: 'app-obras-new',
  templateUrl: './obras-new.component.html',
  styleUrls: ['./obras-new.component.css']
})
export class ObrasNewComponent implements OnInit {

  obra: Obra = new Obra
  tiposColecciones: TipoColeccion[]
  tiposTecnicas: TipoTecnica[]
  tipoColeccion: TipoColeccion
  tipoTecnica: TipoTecnica
  imageFile: File = null
  coleccion: TipoColeccion = new TipoColeccion
  tecnica: TipoTecnica = new TipoTecnica
  agregado: boolean = true

  constructor(private tipoColeccionService: TipoColeccionService,
    private obraService: ObraService,
    private router:Router) { 
    }

  ngOnInit() {
    this.cargarTipos()
  }

  cargarTipos() {
    this.tipoColeccionService.getTiposColeccion().then(tipos => this.tiposColecciones = tipos)
    this.tipoColeccionService.getTiposTecnicas().then(tipos => this.tiposTecnicas = tipos)
  }

  cambioTipoColeccion(nuevoTipo){
    this.tipoColeccion = nuevoTipo
  }

  cambioTipoTecnica(nuevoTipo){
    this.tipoTecnica = nuevoTipo
  }

  changeImage(event) {
    this.imageFile = <File>event.target.files[0]
    //this.obraService.uploadImage(<File>event.target.files[0]).subscribe( result => {
      //console.log(result)
    //})
  }

  agregar(e){
    e.preventDefault();
    if (this.imageFile){
      this.obraService.uploadImage(this.imageFile).subscribe()
      this.obra.IMG_SOURCE = "images/" + this.imageFile.name
      this.obra.ID_TIPO_COLECCION = this.tipoColeccion.ID_TIPO_COLECCION
      this.obra.ID_TIPO_DE_TECNICA = this.tipoTecnica.ID_TIPO_DE_TECNICA
      if (this.validarObra()){
        this.obraService.create(this.obra)
        this.router.navigate(['obras'])
      } else {
        this.agregado = false
        return
      }
    } else {
      this.agregado = false
      return
    }
  }

  agregarColeccion(e){
    if (this.coleccion.COLECCION === ""){
      return 
    }
    this.tipoColeccionService.addTipoColeccion(this.coleccion)
    this.cargarTipos()
    this.coleccion.COLECCION = ""
  }

  agregarTecnica(e){
    if (this.tecnica.TIPO_TECNICA === ""){
      return 
    }
    this.tipoColeccionService.addTipoTecnica(this.tecnica)
    this.cargarTipos()
    this.tecnica.TIPO_TECNICA = ""
  }

  validarObra(){
    if (this.obra.NOMBRE_ARTISTA === "" || this.obra.NOMBRE_DE_OBRA === "" || 
        this.obra.IMG_SOURCE === "" || this.obra.LARGO === 0 || this.obra.ANCHO === 0 ||
        this.obra.YEAR === 0 || this.obra.ID_TIPO_COLECCION === 0 || this.obra.ID_TIPO_DE_TECNICA === 0 ||
        this.obra.ID_TIPO_COLECCION === 0 || this.obra.ID_TIPO_DE_TECNICA === 0)
        return false
    return true
  }

}
