import { Component, OnInit } from '@angular/core';
import {Client} from '../sesion/client'
import {ClientService} from '../sesion/client.service'
import {Router} from '@angular/router';

import {AppComponent} from '../app.component';
@Component({
  selector: 'app-registrar',
  templateUrl: './registrar.component.html',
  styleUrls: ['./registrar.component.css']
})
export class RegistrarComponent implements OnInit {

  clients: Client[];
  constructor(
    private _clientService : ClientService,
    private router:Router
  ) { }

  ngOnInit() {
    this.getClients();
    appComponent :AppComponent

  }

  getClients(){
    this._clientService.getClients().then(clients => this.clients = clients);
  }

  registrar(e)
  {
    e.preventDefault();
    var nombre = e.target.elements[0].value;
    var correo = e.target.elements[1].value;
    var pass = e.target.elements[2].value;
    var confirmPass = e.target.elements[3].value;
    if(pass != confirmPass)
    {
        var error = "PRESENTAR ERROR";
    }
    else
    {
      var correoValido = true; 
      for (var i = 0; i<this.clients.length;i++) {
        var element = this.clients[i];
        if(correo == element.CORREO)
        {
          correoValido = false;
          break;
        }
      }

      if(correoValido)
      {
        var newClient = new Client;
        newClient.CORREO = correo;
        newClient.ID_TIPO_CLIENTE = 2;
        newClient.NOMBRE = nombre;
        newClient.PASSWORD = pass;

        this._clientService.create(newClient)
        this.router.navigate(['login'])

      }

    }
  }

}
